import * as React from "react"
import './web3connect.scss'
import Button from "@material-ui/core/Button";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import bnbIcon from '../../images/icons/bnb.svg';
import snowgeIcon from '../../images/icons/snowge.png';
import {AccountBalanceWalletOutlined} from "@material-ui/icons";
import 'aos/dist/aos.css';

const ConnectAccount = (props) => {

    let AOS;
    React.useEffect(() => {
      const AOS = require("aos");
      AOS.init({
        once: true,
      });
    }, []);

    React.useEffect(() => {
      if (AOS) {
        AOS.refresh();
      }
    });

    const snowgeTokenAddress = '0x5e9280d53f28281ce098c8f64e49f5f5dc9ea185';
    const testnetSnowgeTokenAddress = '0x2b5c0653486b6486acab387eb110dd1753f37d95';
    const snowgeABI = [
        // balanceOf
        {
            "constant": true,
            "inputs": [{"name": "_owner", "type": "address"}],
            "name": "balanceOf",
            "outputs": [{"name": "balance", "type": "uint256"}],
            "type": "function"
        },
        // decimals
        {
            "constant": true,
            "inputs": [],
            "name": "decimals",
            "outputs": [{"name": "", "type": "uint8"}],
            "type": "function"
        }
    ];

    const [snowgeBal, setSnowgeBal] = React.useState();
    const [bnbBal, setBNBBal] = React.useState();
    const [account, setAccount] = React.useState(
        localStorage.getItem("account") || ''
    );
    const [displayAccountInfo, setDisplayAccountInfo] = React.useState(false);
    const [chainId, setChainId] = React.useState();
    const canConnect = typeof window.ethereum !== "undefined";

    React.useEffect(() => {
        if (typeof account !== null && typeof account != 'undefined') {
            localStorage.setItem("account", account);
            getSnowgeBalance(account, chainId);
        }
    }, [account]);

    React.useEffect(() => {
        const checkConnection = async () => {
            // Reload on
            window.ethereum.on('chainChanged', (_chainId) => window.location.reload());
            window.ethereum.on('accountsChanged', function (accounts) {
                connectAccount()
            });
        };
        checkConnection();
        const interval = setInterval(() => {
            getSnowgeBalance(account, chainId)
      }, 10000);
      return () => clearInterval(interval);
    });

    async function connectAccount() {
      if (canConnect) {
        console.log('attempting to connect via web3');
        try {
          const web3 = new window.Web3(window.ethereum);
          // console.log(web3);
          const chainId = await web3.eth.getChainId();
          setChainId(chainId);
          // console.log(chainId);
          if(chainId !== 56 & chainId !== 97) {
            window.ethereum.request({ method: 'wallet_addEthereumChain', params: [{ chainId: '0x38', chainName: 'Binance Smart Chain', nativeCurrency: { name: 'BNB', symbol: 'BNB', decimals: 18 }, rpcUrls: ['https://bsc-dataseed.binance.org/'], blockExplorerUrls: ['https://bscscan.com/'] }] });
          }
          const accounts = await web3.eth.requestAccounts();
          const account = accounts[0];
          console.log(account + " connected.");

          setAccount(account);
          getSnowgeBalance(account, chainId);
        } catch(err) {
          console.log('page loaded before web3, need to fix this' + err)
        }
      }
    }

    async function disconnectAccount() {
        setAccount(null);
        setBNBBal(null);
        setSnowgeBal(null);
        setDisplayAccountInfo(false);
        localStorage.removeItem("account");
    }

    async function getSnowgeBalance(account, chainId) {
      if (canConnect) {
        try {
          const web3 = new window.Web3(window.ethereum);
          var contract;
          if (chainId === 56) {
            contract = new web3.eth.Contract(snowgeABI, snowgeTokenAddress);
          } else if (chainId === 97) {
            contract = new web3.eth.Contract(snowgeABI, testnetSnowgeTokenAddress);
          } else {
            return;
          }
          contract.methods.balanceOf(account).call((error, balance) => {
            contract.methods.decimals().call((error, decimals) => {
              balance = balance / (10**decimals);
              setSnowgeBal(balance.toFixed(4).toString());
              // console.log(balance.toString());
            });
          });
          let bnbBal = await web3.eth.getBalance(account);;
          let bnbString = (bnbBal / (10**18)).toFixed(4).toString();
          setBNBBal(bnbString);
        } catch(err) {
          console.log('page loaded before web3, need to fix this' + err)
        }
      }
    }

    return (
        <div className={`${account ? 'account-connected' : 'no-account'}`}>
            {(!account || account == "null" || account == null || account == "undefined") &&
            <Tooltip title="Connect wallet" enterDelay={600}>
                <IconButton aria-label="connect wallet" className={`${props.desktop} hoverscale`}
                            onClick={connectAccount}>
                    <AccountBalanceWalletOutlined
                        className={props.activeNavTextColour}/>
                </IconButton>
            </Tooltip>
            }

            {account && account != "null" &&
            <Tooltip title={!displayAccountInfo ? "Show Wallet Information" : "Hide Wallet Information"} enterDelay={600}>
                <IconButton aria-label="wallet information" className={`${props.desktop} hoverscale`}
                            onClick={() => setDisplayAccountInfo(!displayAccountInfo)}>
                    <AccountBalanceWalletOutlined
                        className={'account-connected-icon'}/>
                </IconButton>
            </Tooltip>
            }

            {displayAccountInfo &&
            <div className={'wallet-information-card swing-in-top-fwd'}>
                <div className={'flex-column'}>
                    <div className={'wallet-address flex-row x-centre'}>{account}</div>
                    <div className={'wallet-info-row'}>
                        <span className="wallet-info-text-bold"><img src={snowgeIcon} className="currency-icon snowge-currency-icon"/>SnowgeCoin balance:&nbsp;</span>
                        <span className={'wallet-info-text'}>{snowgeBal}</span>
                    </div>
                    <div className={'wallet-info-row'}>
                        <span className="wallet-info-text-bold"><img src={bnbIcon} className="currency-icon"/>BNB balance:&nbsp;</span>
                        <span className={'wallet-info-text'}>{bnbBal}</span>
                    </div>
                    {!(snowgeBal && bnbBal) && <div className={'flex-row x-centre smol-text'} data-aos='flip-up' data-aos-delay='5000'>Balance not appearing? Try disconnecting and reconnecting your wallet.</div>}
                    <div className={'disconnect-button flex-row x-centre'}>
                        <Button color="secondary" style={{ fontSize: '12px', textTransform: 'none' }} className="hoverscale" onClick={disconnectAccount}>Disconnect Wallet</Button>
                    </div>
                </div>
            </div>
            }

            {/*<div className={'connect-button-status'}>*/}
            {/*    {!account &&*/}
            {/*    <Button className={'connect-wallet-button'} onClick={connectAccount}>Connect Wallet</Button>}*/}
            {/*    {account && <Tooltip title={<span*/}
            {/*        style={{whiteSpace: 'pre-line'}}>{`Account: ` + account + `\nSnowgeCoin Balance: ` + snowgeBal + `\nBNB Balance: ` + bnbBal}</span>}*/}
            {/*                         enterTouchDelay={0} leaveTouchDelay={4000} interactive={true} enterDelay={200}>*/}
            {/*        <div className={`connected-text ${props.activeNavTextColour}`}>Connected.</div>*/}
            {/*    </Tooltip>}*/}
            {/*    {account && <Button className={`connect-wallet-button disconnect-button`} onClick={disconnectAccount}>Disconnect*/}
            {/*        Wallet</Button>}*/}
            {/*</div>*/}
        </div>
    )
};

export default ConnectAccount
